.footer {

    a {
        color: $color;
    }

    background-color: $footer-background-color;
    color: $color;
    line-height: 1.5;

    ul {
      list-style: none;
      padding-left: 0;
    }

    footer a {
        text-decoration: none;
    }

    .ft-title {
        font-size: 1.375rem;
        padding-bottom: 0.625rem;
    }

    .container {
        flex: 1;
    }
    /* Footer main */
    .ft-main {
        padding: 1.25rem 1.875rem;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }

    @media all and (max-width: $breaking-point-tablet) {
        .ft-main {
            justify-content: flex-start;
            flex-direction: column;
        }
    }

    .ft-main-item {
        padding: 1.25rem 0;
        min-width: 12.5rem;
    }

    /* Footer social */
    .ft-social {
        padding: 0 1.875rem 1.25rem;
    }
    .ft-social-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        border-top: 1px $color solid;
        padding-top: 1.25rem;
    }
    .ft-social-list li {
        margin: 0.5rem;
        font-size: 1.25rem;
    }
    /* Footer legal */
    .ft-legal {
        padding: 0.9375rem 1.875rem;
        background-color: $footer-background-color-bottom;
    }
    .ft-legal-list {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
    }
    .ft-legal-list li {
        margin: 0.125rem 0.625rem;
        white-space: nowrap;
    }
    /* one before the last child */
    .ft-legal-list li:nth-last-child(2) {
        flex: 1;
    }
}
