#restaurant {

    background-image: url("../assets/images/restaurant.jpg");
    background-size: cover;
    filter: grayscale(25%);
    color: $restaurant-color;

    .wrapper {
        // padding: 25px;
        border-radius: 20px;
        // text-align: right;
        background: $restaurant-background-color;
    }

    .container {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        // grid-gap: 25px;
    }
    
    .container-item {
        // flex: 1 1 0px;
        // flex: 0 1 auto;
        padding: 25px;
    }
    
    /* Responsive layout - makes a one column layout instead of a two-column layout */
    @media all and (max-width: $breaking-point-tablet) {
        .container {
            flex-direction: column;
        }

        .container-item {
            // flex: 1 1 0px;
            // flex: 0 1 auto;
            margin-top: 53px;
            margin: 25px 0;
        }
        

    }
    
    table {
        border-collapse:separate; 
        border-spacing: 0 1em;
        margin: 0 auto;
    }
    
    h2 {
        margin-bottom: 20px;
        text-decoration-line: underline;
        border: 1x solid black;
        text-align: center;
    }
    
    th {
        padding-right: 10px;
        text-align: right;
        vertical-align: top;
    }
    
}

