.insta {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    img {
        width: 100%;
        border-radius: $border-radius;
    }

}


.insta-item {
    display: flex;
    flex-basis: calc(33% - 10px);
    margin: 5px;
}

@media all and (max-width: $breaking-point-phone) {
    .insta {
        flex-direction: column;
    }

    .insta-item {
        flex: 1;
    }
}
@media all and (max-width: $breaking-point-tablet) {
    .insta-item {
        flex-basis: calc(50% - 10px);
    }
}

