.p-button {
    background-color: $primary; /* Green */
    border: none;
    color: $color;
    padding: 16px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: $border-radius;
}

.p-button-1 {
    background-color: $primary; 
    color: $color; 
    border: 2px solid $primary;
  }