.slideshow {
    .mySlides {
        display: none;
    }
    img {
        vertical-align: middle;
    }

    .image {
        width: 100%;
        height: 350px;
        object-fit: contain;
    }

    /* Slideshow container */
    .slideshow-container {
        position: relative;
        margin: auto;
        height: 100%;
        width: 100%;
        border-radius: $border-radius;
    }

    /* Next & previous buttons */
    .prev,
    .next {
        cursor: pointer;
        position: absolute;
        top: 0;
        width: 50%;
        height: 100%;
    }

    .prev-left,
    .next-right {
        cursor: pointer;
        position: absolute;
        top: 50%;
        width: auto;
        padding: 16px;
        margin-top: -22px;
        color: $color;
        font-weight: bold;
        font-size: 18px;
        border-radius: 0 $border-radius $border-radius 0;
        user-select: none;
    }

    /* Position the "next button" to the right */
    .next {
        right: 0;
    }

    .next-right {
        right: 0;
    }

    /* Caption text */
    .text {
        color: #f2f2f2;
        font-size: 15px;
        padding: 8px 0px;
        position: absolute;
        bottom: 8px;
        width: 100%;
        text-align: center;
    }

    /* Number text (1/3 etc) */
    .numbertext {
        color: #f2f2f2;
        font-size: 12px;
        padding: 8px 12px;
        position: absolute;
        top: 0;
    }

    /* The dots/bullets/indicators */
    .dot {
        cursor: pointer;
        height: 15px;
        width: 15px;
        margin: 0 2px;
        background-color: #bbb;
        border-radius: 50%;
        display: inline-block;
        transition: background-color 0.6s ease;
    }

    .active {
        background-color: #717171;
    }

    /* Fading animation */
    .fade {
        -webkit-animation-name: fade;
        -webkit-animation-duration: 1.5s;
        animation-name: fade;
        animation-duration: 1.5s;
    }

    @-webkit-keyframes fade {
        from {
            opacity: 0.4;
        }
        to {
            opacity: 1;
        }
    }

    @keyframes fade {
        from {
            opacity: 0.4;
        }
        to {
            opacity: 1;
        }
    }
}
