#about {
    h1 {
        margin-top: 20px;
        margin-bottom: 10px;
    }

    p {
        margin-bottom: 1rem;
    }

    img {
        float: right;
        margin-left: 20px;
        margin-bottom: 20px;
        border-radius: 10px;
        width: 50%;
    }
}

@media all and (max-width: $breaking-point-tablet) {
    #about {
        img {
            float: none;
            width: 100%;
            margin: 0;
        }
    }
}
