#menu {
    .language {
        .card-body {
            display: flex;
            justify-content: space-evenly;
            align-items: center;
        }

        img {
            width: 40px;
            height: 40px;
            border-radius: $border-radius;
            margin: 5px;
        }

        .active {
            background-color: $primary;
            border: 1px solid $primary;
            padding: 5px;
            margin: 0;
        }
    }

    .category-title {
        text-align: center;
        font-size: 2rem;
        text-decoration: underline;
        margin-bottom: 50px;
        margin-top: 75px;
    }

    .food-container {
        display: flex;
        flex-direction: row;
        margin-bottom: 20px;
        padding-bottom: 20px;
        border-bottom: 1px dotted $menu-food-border-color;
    }

    .food-description {
        flex: 1;
        // display: flex;
        // flex-direction: column;

        .food-title {
            font-weight: bold;
            font-size: 1.33rem;
            color: $menu-food-title-color;
        }

        .food-subtitle {
            color: $menu-food-subtitle-color;
            font-size: 1rem;
        }

        .food-allergies {
            padding-top: 10px;
            img {
                margin-right: 4px;
                vertical-align: bottom;
                border-radius: 25px;
                height: 27px;
                width: 27px;
                overflow: hidden;
            }
        }
    }

    .food-info {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: space-between;

        .food-price {
            font-weight: bold;
            color: $menu-food-price-color;
        }

        .food-special {
            background-color: $menu-food-special-background;
            color: $menu-food-special-color;
            padding: 2px 10px;
            font-style: italic;
            font-size: 0.9rem;
            border-radius: 10px;
        }
    }

    .image {
        width: 100%;
        border-radius: 10px;
    }

    .carne-title {
        margin-top: 20px;
        font-weight: bold;
        font-size: 1.33rem;
        color: $menu-food-title-color;
    }

    .carne-subtitle {
        margin-top: 5px;
        color: $menu-food-subtitle-color;
    }

    .tripadvistor {
        margin-top: 40px;
        text-align: center;

        a {
            text-decoration: none;
            color: $menu-tripadvisor-color;
            
        }

        img {
            width: 80%;
            border: 1px solid $primary;
            border-radius: 20px;
            padding: 10px;
            background-color: $menu-tripadvisor-background-color;
        }

        @media all and (min-width: $breaking-point-phone) {
            img {
                width: 50%;
                
            }
        }
    }
}
