.landing-page {
    height: calc(100vh - 53px);
    margin-top: 53px;
    padding-left: 25px;
    padding-right: 25px;
    
    background-image: url('../assets/images/landing.jpg');
    background-size: cover;
    background-position: right top;
    filter: grayscale(25%);
    
    color: $color;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;

    // h1 {
    //     text-shadow: 1px 1px 2px rgba(0,0,0,0.4);
    // }

    .wrapper {
        margin-top: 53px;
        padding: 25px;
        border-radius: $border-radius;
        text-align: right;
        text-shadow:
            0.07em 0 black,
            0 0.07em black,
            -0.07em 0 black,
            0 -0.07em black;
    }
}
