#awards {
    ul {
        display: flex;
        flex-flow: row wrap;
        list-style: none;
        align-items: center;
    }

    li {
        margin: 30px 20px 30px 20px;
        flex-basis: calc(25% - 40px);

        img {
            width: 100%;
            height: auto;
            border-radius: $border-radius;
        }
    }

    @media all and (max-width: $breaking-point-tablet) {
        li {
            margin: 30px 20px 30px 20px;
            flex-basis: calc(50% - 40px);
        }
    }
}
