/* Basic styling */

nav {
    background: $nav-background-color;
    position: fixed;
    top: 0px;
    width: 100%;
    z-index: 1;

    .toggle {
        height: 29px;
    }

    ul {
        padding-left: 20px;
        list-style-type: none;
    }
    
    a {
        color: $nav-color;
        text-decoration: none;        
    }
    
    a:hover {
        text-decoration: underline;
    }
    
    .logo a:hover {
        text-decoration: none;
    }
    
    .menu li {
        font-size: 16px;
        padding: 15px 5px;
        white-space: nowrap;
    }
    
    .logo a,
    .toggle a {
        font-size: 20px;
    }
    
    /* Mobile menu */
    
    .menu {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
    }
    
    .toggle {
        order: 1;
    }
    
    .item.button {
        order: 2;
    }
    
    .item {
        width: 100%;
        text-align: center;
        order: 3;
        display: none;
    }
    
    .item.active {
        display: block;
    }
    
    
    /* Navbar Toggle */
    
    .toggle {
        cursor: pointer;
    }
    
    .bars {
        background: $nav-bars-color;
        display: inline-block;
        height: 2px;
        position: relative;
        width: 18px;
        margin-left: 15px;
    }
    
    .bars:before,
    .bars:after {
        background: $nav-bars-color;
        content: '';
        display: block;
        height: 100%;
        position: absolute;
        width: 100%;
    }
    
    .bars:before {
        top: 5px;
    }
    
    .bars:after {
        top: -5px;
    }
    
    
    /* Tablet menu */
    
    @media all and (min-width: $breaking-point-phone) {

        .menu {
            justify-content: center;
        }
        .logo {
            flex: 1;
        }
        .item.button {
            width: auto;
            order: 1;
            display: block;
        }
        .toggle {
            order: 2;
        }
        .button.secondary {
            border: 0;
        }
        .button a {
            padding: 7.5px 15px;
            background: $nav-button-color;
            border: 1px $nav-button-color solid;
            border-radius: 50em;
        }
        .button.secondary a {
            background: transparent;
        }
        .button a:hover {
            text-decoration: none;
            transition: all .25s;
        }
        .button:not(.secondary) a:hover {
            background: $nav-button-color-hover;
            border-color: $nav-button-color-hover;
        }
    }
    
    
    /* Desktop menu */
    
    @media all and (min-width: $breaking-point-tablet) {

        .item {
            display: block;
            width: auto;
        }
        .toggle {
            display: none;
        }
        .logo {
            order: 0;
        }
        .item {
            order: 1;
        }
        .button {
            order: 2;
        }
        .menu li {
            padding: 15px 10px;
        }
        .menu li.button {
            padding-right: 0;
        }
        
    }

    @media all and (max-width: $breaking-point-tablet) {

        a {
            display: block;
            padding: 15px 10px; 
        }

        .menu li {
            padding: 0px;
        }
        
    }
}

