.accordion{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    .a-container{
        display: flex;
        flex-direction: column;
        width: 100%;
        padding-bottom: 5px;

        .a-btn{
            font-size: 22px;
            text-align: center;
            position: relative;
            margin: 0;
            padding: 15px 0 15px;
            width: 100%;
            color: $color;
            font-weight: 400;
            display: block;
            font-weight: 500;
            background-color: $primary;
            cursor: pointer;
            // transition: all 0.3s ease-in-out;
            border-radius: $border-radius;
            box-shadow: 0 20px 25px -5px rgba(0,0,0,.15),0 10px 10px -5px rgba(0,0,0,.1)!important;
            span{
                display: block;
                position: absolute;
                height: 14px;
                width: 14px;
                right: 20px;
                top: 22px;
                &:after{
                    content: '';
                    width: 14px;
                    height: 3px;
                    border-radius: $border-radius;
                    background-color: #fff;
                    position: absolute;
                    top: 6px;
                }
                &:before{
                    content: '';
                    width: 14px;
                    height: 3px;
                    border-radius: $border-radius;
                    background-color: #fff;
                    position: absolute;
                    top: 6px;
                    transform: rotate(90deg);
                    transition: all 0.3s ease-in-out;
                }
            }
            
        }
        .a-panel{
            // width: 100%;       
            color: $primary;
            transition: all 0.2s ease-in-out;
            opacity: 0;
            height: auto;
            max-height: 0;
            overflow: hidden;
            padding: 0px 10px;
        }


        &.active{
            .a-btn{
                color: #fff;
                span{
                    &::before{
                        transform: rotate(0deg);
                    }
                }
            }
            .a-panel{               
                // padding: 15px 10px 10px 10px;
                padding-top: 15px;
                opacity: 1;
                max-height: max-content;              
            }
        }
    }
}